/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-utk-wds-tab {
	--nav-link-padding-x: 1rem;
	--nav-link-padding-y: 0.5rem;
	--nav-tabs-border-radius: 0px;

	margin: 1rem;

	.tab-name.rich-text {
		display: inline-block;
		font-weight: 700;
		font-size: var(--wp--preset--font-size--normal);
		border: 1px solid var(--nav-tabs-border-color);
		border-bottom-color: var(--wp--preset--color--light);
		padding: var(--nav-link-padding-y) var(--nav-link-padding-x);
		margin: 0;
		background-color: var(--wp--preset--color--light);
		border-top-left-radius: var(--nav-tabs-border-radius);
		border-top-right-radius: var(--nav-tabs-border-radius);
	}
	.utk-wds-tab__panel-body {
		border: 1px solid var(--nav-tabs-border-color);
		padding-left: var(--nav-link-padding-x);
		padding-right: var(--nav-link-padding-x);
		background-color: var(--wp--preset--color--light);
	}

	.block-editor-block-list__layout > *:first-child {
		margin-top: 0;
		padding-top: 20px;
	}
}
